/**
 * API
 */
export default class WxApi{
    static get isRunInWeiXin(){
        if(cc.sys.platform === cc.sys.WECHAT_GAME){
            return true
        }else{
            WxApi.errorLog();
            return false;
        } 
    }

    static errorLog(func){
        console.warn("[]");
    }
}