import { IConstructorName } from "../IocConst";

//namespace ioc {
export interface IBinding {
    name: any;
    key: any;
    value: any;
    bind(k:any):IBinding;
    to(v:any):IBinding;
    toName(n:any):IBinding;
}
export class __IC_Binding extends IConstructorName{
    get constructorName(){return "IBinding";}
}
export class Binding implements IBinding {
    protected _key: any;
    protected _value: any;
    protected _name: any;
    protected _resolver: any;

    public get key() {
        return this._key;
    }
    public get value() {
        return this._value;
    }
    public get name() {
        return this._name;
    }

    constructor(resolver: any) {
        this._resolver = resolver;

        this._key = null;
        this._value = null;
        this._name = null;
    }

    /**
     * 
     * @param key 
     */
    bind(key) :IBinding{
        this._key = Binding.checkAbstract(key);
        return this;
    }
    /**
     * 
     * @param value 
     */
    to(value):IBinding {
        this._value = value;
        if (this._resolver != null)
            this._resolver(this);
        return this;
    }

    /**
     * 
     * @param name 
     */
    toName(name):IBinding {
        this._name = name;
        if (this._resolver != null)
            this._resolver(this);
        return this;
    }
    //
    public get isKeyConstructor():boolean{
        return Binding.isConstructor(this._key);
    }
    //
    public get isValueConstructor():boolean{
        return Binding.isConstructor(this._value);
    }
    public static isConstructor(value : object):boolean{
        //
        if(typeof value !== "function"){
            return false;
        }
        //
        if(!value.prototype){
            return false;
        }
        return true;
    }
    public static checkAbstract(key){
        //
        let constructorName;
        if(key.constructorName){
            constructorName = key.constructorName;
            //console.info("[]"+ constructorName);
        }else{
            constructorName = key;
            //console.info("[]"+ constructorName.constructor.name);
        }
        return constructorName;
    }
}
