export interface IStack<T> {
    //
    top(): T;
    //
    push(item: T);
    //
    pop(): T;
    //
    clear();
    //
    isEmpty: boolean;
    //
    size: number;
}

export class Item<T> {
    private _value: T;
    private _next: Item<T>;
    constructor(value: T, next: Item<T> = null) {
        this._value = value;
        this._next = next;
    }
    set value(value: T) {
        this._value = value;
    }
    get value(): T {
        return this._value;
    }
    set next(next: Item<T>) {
        this._next = next;
    }
    get next(): Item<T> {
        return this._next;
    }
}

export class Stack<T> implements IStack<T> {
    private _header: Item<T>;
    private _size: number = 0;
    constructor() {
        this._header = new Item<T>(null);
    }
    top(): T {
        if (this._size === 0) {
            return null;
        }
        return this._header.next.value;
    }

    /**
     * 
     * @param item 
     * headernext
     * headernext
     */
    push(item: T) {
        let newItem = new Item<T>(item);
        newItem.next = this._header.next;
        this._header.next = newItem;
        this._size++;
    }

    /**
     * 
     * header
     * headernext
     */
    pop(): T {
        if (this._size === 0) {
            return null;
        }
        let item = this._header.next;
        this._header.next = item.next;
        this._size--;
        item.next = null;//
        return item.value;
    }
    clear(){
        let item;
        let tmp = this._header;
        while(this._size !== 0){
            item = tmp.next;
            tmp = item;
            item.next = null;
            this._size--;
        }
        this._header = null;
    }
    get isEmpty(): boolean {
        return this._size === 0;
    }

    get size(): number {
        return this._size;
    }
}