import Common from "../../common";
import WxApi from "../WxApi";

cc.Class({
    extends: cc.Component,

    properties: {
        subContextView: cc.Node,
        tipsUiNode: cc.Node
    },

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        let that = this;
        this.rankType = 0;
        this.shareCount = 0;
        //
        if (WxApi.isRunInWeiXin) {
            wx.updateShareMenu({
                withShareTicket: true,
                success() {
                    console.info("");
                },
                fail() {
                    console.info("");
                }
            })
        }
        //this.subContextView.enabled = false;
        console.info("[]");
    },

    start() {
        console.info("[]");
        //
        if (WxApi.isRunInWeiXin) {
            console.info("[]" + this.subContextView.width + "," + this.subContextView.height);
            wx.getOpenDataContext().postMessage({
                message: {
                    type: "command",
                    function: "start",
                    arguments: "default",
                    data: {
                        width: this.subContextView.width,
                        height: this.subContextView.height
                    },
                }
            });
        }
    },
    //
    switchRank(event) {
        //
        let switchBtn = event.target;
        let switchBar = event.target.getChildByName("Switch");
        let moveRange = switchBtn.width - switchBar.width - 10;
        let that = this;
        //
        if (this.rankType < 1) {
            this.rankType += 1;
        } else {
            this.rankType = 0;
        }
        switch (this.rankType) {
            case 0:
                switchBar.runAction(cc.moveBy(0.125, cc.v2(-moveRange, 0)));
                if (WxApi.isRunInWeiXin) {
                    console.info("[]");
                    //
                    wx.getOpenDataContext().postMessage({
                        message: {
                            type: "command",
                            function: "switch",
                            arguments: "friend",
                            data: "",
                        }
                    });
                }
                break;
            case 1:
                switchBar.runAction(cc.moveBy(0.125, cc.v2(moveRange, 0)));
                if (WxApi.isRunInWeiXin) {
                    let ticket;
                    if (Common.shareTickets && Common.shareTickets.length > 0) {
                        ticket = Common.shareTickets[this.shareCount];
                        if (!ticket) {
                            console.info("[]" + Common.shareTickets +","+ this.shareCount);
                            Common.shareTickets = [];
                            this.shareCount = 0;
                        } else {
                            //0
                            if (this.shareCount >= (Common.shareTickets.length - 1)) {
                                this.shareCount = 0;
                            } else {
                                this.shareCount += 1;
                            }
                        }
                    } else {
                        console.info("[]");
                        this.showTipsUi("", "");
                    }
                    //
                    if(ticket&&ticket!=""){
                        wx.getShareInfo({
                            shareTicket : ticket,
                            success(res){
                                console.info("[]" + ticket + "," + res);
                            },
                            fail(err){
                                console.info("[]" + ticket + "," + err);
                            }
                        })
                    }else{
                        ticket = "";
                    }
                    console.info("[]" + Common.shareTickets.length);
                    //
                    wx.getOpenDataContext().postMessage({
                        message: {
                            type: "command",
                            function: "switch",
                            arguments: "group",
                            data: ticket,
                        }
                    });
                }
                break;
            default:
                break;
        }
        console.info("[]" + this.rankType);
    },

    backhome() {
        cc.director.loadScene("Begin");
    },

    shareApp() {
        if (!WxApi.isRunInWeiXin) return;
        let that = this;
        wx.shareAppMessage({
            title: "",
            success(res) {
                console.info("[]" + res.shareTickets);
                //
                if (!res.shareTickets || res.shareTickets === "") {
                    console.info("[]");
                } else {
                    console.info("[]" + res.shareTickets);
                    //
                    if (res.shareTickets.length > 0) {
                        let isExist = false;
                        //
                        for (let i = 0; i < res.shareTickets.length; i++) {
                            for (let j = 0; j < Common.shareTickets.length; j++) {
                                //
                                if (res.shareTickets[i] === Common.shareTickets[j]) {
                                    isExist = true;
                                    break;
                                }
                            }
                            if (!isExist) {
                                console.info("[]" + res.shareTickets[i]);
                                Common.shareTickets.push(res.shareTickets[i]);
                            }
                        }
                    }
                }
            },
            fail(res) {
                console.info("[]");
            }
        });

    },
    //
    showTipsUi(title, content) {
        if (!this.tipsUiNode.active) {
            this.tipsUiNode.active = true;
            let tipsUiView = this.tipsUiNode.getComponent("TipsUiView");
            if (tipsUiView) {
                tipsUiView.display(title, content);
            } else {
                console.info("[UI]");
            }
        }
    },
});